import React from 'react';
import './helpmap.css';
import MypageMap from './kakaomap';



//  
const SearchBar = () => {
  return (
    <div className="search">
      <label htmlFor="searchfor"> |</label>
      <input type="text" placeholder=" " />
      <div className="house_type_buttons">
        <span>/</span>
        <span>|</span>
        <button></button>
        <button></button>
        <button></button>
      </div>
    </div>
  );
};

const Helppage = () => {
  return (
    <div>
      <SearchBar />
      <MypageMap/>
    </div>
  );
};

export default Helppage;
