/*global kakao*/
//  api 
import "./kakaomap.css";
import React, { useEffect } from "react";
import {Route,Routes } from 'react-router-dom';
import {Link} from 'react-router-dom';
import Info from './helpinfo';
//import Swal from "sweetalert2";
//import dummy from "..data.json"



const MypageMap = () => {
  //   !!!!!!!!!!!!!!!!!!!!!
     //         
     var positions = [
      {
          content: '',
          money1 : '300',
          money2 : '30' ,
          stair : '6',
          size : '23',
          latlng: new kakao.maps.LatLng(33.450705, 126.570677)
      },
      {
          content: '',
          money1 : '300',
          money2 : '30' ,
          stair : '6',
          size : '23', 
          latlng: new kakao.maps.LatLng(33.450936, 126.569477)
      },
      {
          content: '',
          money1 : '300',
          money2 : '30' ,
          stair : '6',
          size : '23', 
          latlng: new kakao.maps.LatLng(33.450879, 126.569940)
      },
      {
          content: '',
          money1 : '300',
          money2 : '30' ,
          stair : '6',
          size : '23',
          latlng: new kakao.maps.LatLng(33.451393, 126.570738)
      }
  ];

  useEffect(() => { 
    //   
    try {
      var markers = [];

      var mapContainer = document.getElementById("map"); //   div

      var mapOption = {
        center: new kakao.maps.LatLng(33.450701, 126.570667), //  
        level: 3, //   
      };

      //  
      var map = new kakao.maps.Map(mapContainer, mapOption);

      //           
      var mapTypeControl = new kakao.maps.MapTypeControl();

      //     
      // kakao.maps.ControlPosition     TOPRIGHT   
      map.addControl(mapTypeControl, kakao.maps.ControlPosition.TOPRIGHT);

      //          
      var zoomControl = new kakao.maps.ZoomControl();
      map.addControl(zoomControl, kakao.maps.ControlPosition.RIGHT);   


      for (var i = 0; i < positions.length; i ++) {
          //  
          var marker = new kakao.maps.Marker({
              map: map, //   
              position: positions[i].latlng //  
          });

          //     
          var infowindow = new kakao.maps.InfoWindow({
              content: positions[i].content //   
          });

          //  mouseover  mouseout  
          //     
          // for        
          kakao.maps.event.addListener(marker, 'mouseover', makeOverListener(map, marker, infowindow));
          kakao.maps.event.addListener(marker, 'mouseout', makeOutListener(infowindow));
      }

      //      
      function makeOverListener(map, marker, infowindow) {
          return function() {
              infowindow.open(map, marker);
          };
      }
      //     
      function makeOutListener(infowindow) {
          return function() {
              infowindow.close();
          };
      }
    }


    catch (err) {
      console.log(err);
    }
    
  }, []);

  return (
    <div className="map_wrap">
      <div id="map"></div>
      <div id="menu_wrap" className="bg_white">
        <ul id="placesList">
          {positions.map((position, index) => (
           <li key={index}>
            <h4 id="housename"><Link to ='/helpinfo'>{position.content}</Link></h4>
            <p>:{Number(position.money1)}/{Number(position.money2)}</p>
            <p>:{Number(position.stair)}</p>
            <p>:{Number(position.money1)}m3</p>
           </li>
           ))}
        </ul>
        <Routes>
          <Route path="/helpinfo" element= {<Info/>}/>
        </Routes>
        <div id="pagination"></div>
      </div>
    </div>
  );
};
export default MypageMap;